import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';

/**
 * Generated class for the MDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name:'detail',
  segment:'detail/:id',
})
@Component({
  selector: 'page-m-detail',
  templateUrl: 'm-detail.html',
})
export class MDetailPage {
  public movie:any;
  public id:number;
  public categories:any;
  public episodes:any;
  constructor(public navCtrl: NavController,
    public api:ApiProvider,
     public navParams: NavParams) {

    this.id = this.navParams.get('id');
    console.log( this.movie);
    
  }

   async  ionViewDidLoad() {
    let detail:any = await this.api.get_movie_detail(this.id);
    this.movie = detail.movie[0];
    this.categories = detail.categories
    this.episodes = detail.list_episode;
    console.log(detail);
  }

  push_play(movie,episodes)
  {
    this.navCtrl.push('play',{movie_id : movie.id,episode_id : episodes[0].episode});
  }

}



